<template>
    <div>
        <ul class="cart__orders">
            <CartOrderItem v-for="item in products" :key="item.productId" :item="item"></CartOrderItem>
        </ul>
        <div class="cart__total">
        <p>Доставка: <b>500 ₽</b></p>
        <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{totalPrice | numberFormat}} ₽</b></p>
        </div>
    </div>

</template>

<script>
    import CartOrderItem from '@/components/CartOrderItem.vue';
    import { mapGetters } from 'vuex';
    import numberFormat from '@/helpers/numberFormat'

    export default {
        components: {CartOrderItem},
        filters:{
          numberFormat
        },
        computed: {
        ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),

        }  
    }
</script>