<template>
    <div>
        <ul class="cart__orders">
            <BasketOrderItem v-for="item in products" :key="item.productId" :item="item"></BasketOrderItem>
        </ul>
        <div class="cart__total">
        <p>Доставка: <b>500 ₽</b></p>
        <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{totalPrice | numberFormat}} ₽</b></p>
        </div>
    </div>

</template>

<script>
    import BasketOrderItem from '@/components/BasketOrderItem.vue';
    import { mapGetters } from 'vuex';
    import numberFormat from '@/helpers/numberFormat'

    export default {
        filters:{
          numberFormat
        },
        components: {BasketOrderItem},
        computed: {
        ...mapGetters({ products: 'basketDetailProducts', totalPrice: 'basketTotalPrice' })
        }  
    }
</script>