<template>
  <fieldset class="form__block">
          <legend class="form__legend">Цвет</legend>
              <ul class="colors">
               <div v-for="(itemcolor, index) in color" :key="index">
                  <li class="colors__item" >
                  <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio" :value="itemcolor" v-model="currentColor">
                      <span class="colors__value" :style="{ 'background-color': itemcolor}" >
                      </span>
                  </label>
                  </li>
              </div>

<!--                <ColorItem :color= "color"></ColorItem>
 -->            
            </ul>
  </fieldset>          
</template>

<script>

    export default {
      props:['colorArray'],
       data(){
          return {
            currentColor: "",
          }
        },
      watch: {
        currentColor(){
          this.$emit('color-event', this.currentColor)
        }
      },
      methods: {
      }, 
      computed: {
        color(){
          return this.colorArray.map((color) => color.code).slice(0, this.colorArray.length - 1)
        }
      }
     
    }
</script>
