<template>
      <li class="catalog__item">
        <router-link class="catalog__pic" href="#" :to="{name: 'product',  params:{id: product.id}}">
          <img v-bind:src="product.image" :alt="product.title">
        </router-link>

        <h3 class="catalog__title"> 
          <a href="#">
            {{product.title}}
          </a>
        </h3>

        <span class="catalog__price">
          {{product.price | numberFormat}} ₽
        </span>



          <ul class="colors colors--black">

          <div v-for="(itemcolor, index) in product.colors" :key="index">
            <li class="colors__item" >
              <label class="colors__label">
                <input class="colors__radio sr-only" type="radio" :value="itemcolor" v-model="color">
                <span class="colors__value" :style="{ 'background-color': itemcolor}" >
                </span>
              </label>
            </li>
          </div>  


        </ul>


      </li>

</template>

<script>
import gotoPage from "@/helpers/gotoPage"
import numberFormat from '@/helpers/numberFormat'

export default {
    data(){
      return{
        color: '#73B6EA'
      }
    },
    filters:{
      numberFormat
    },
    methods: {
      gotoPage
    },
    props: ['product']
}
</script>