<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCatergoryId" :color.sync="filterColor"></ProductFilter>
      <section class="catalog">
        <div v-if="producatsLoading">
          <img src="../images/Iphone-spinner-2.gif">
        </div>
        <div v-if="producatsLoadingFailed">Произошла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
        <ProductList v-bind:products="products" ></ProductList>
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"></BasePagination>

      </section>
    </div>
  </main>


</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import {API_BASE_URL} from "../config";

export default {
    components: { ProductList, BasePagination, ProductFilter },
    comments: { ProductList: ProductList, BasePagination, ProductFilter },
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCatergoryId: 0,
            filterColor: "",
            page: 1, 
            productsPerPage: 3,
            productsData: null,
            producatsLoading: false,
            producatsLoadingFailed: false
        };
    },
    computed: {
      products(){
        return this.productsData
         ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url,
            colors: product.colors.map((color) => color.code)
          }
         })
         : [] 
      },
      countProducts(){
        return this.productsData? this.productsData.pagination.total: 0
      }

    },
    methods: {
      loadProducts() {
          this.producatsLoading = true
          this.producatsLoadingFailed = false
          clearTimeout(this.loadProductsTimer)
          this.loadProductsTimer = setTimeout(() => {
            axios.get(API_BASE_URL + '/api/products', { 
                params: {
                  page: this.page,
                  limit: this.productsPerPage,
                  categoryId: this.filterCatergoryId,
                  minPrice: this.filterPriceFrom,
                  maxPrice: this.filterPriceTo,
                  colorId: this.filterColor
                }
            })
            .then(response => this.productsData = response.data)
            .catch(() => this.producatsLoadingFailed = true)
            .then(() => this.producatsLoading = false)
          }, 0);
      }
    },
    watch: {
      page(){
        this.loadProducts()
      },
      filterPriceFrom() {
        this.loadProducts()
      },
      filterPriceTo() {
        this.loadProducts()
      },
      filterCatergoryId() { 
        this.loadProducts()
      },
      filterColor() { 
        this.loadProducts()
      }
      
    },
    created(){
        this.loadProducts()
      }
    
}

</script>